//-<  >-===================================================
body {
     font-size: 32px;
     font-family: $font-family;
     font-weight: 600;
     color: #ffff;
     background-color: #000;

     &._lock {
          overflow: hidden;
     }
}

//-<  >-===================================================

//<Wrapper   >-========================================================================
.wrapper {
     display: flex;
     flex-direction: column;
     min-height: 100%;
     width: 100%;
}

//---------------------------------------
._container {
     max-width: $maxWidthContainer + px;
     padding: 0 10px;
     margin: 0 auto;
     overflow: hidden;
}

._container-project {
     position: relative;
     overflow: hidden;
     margin: 0 auto;

     @media (min-width: 850px) {
          padding: 0 10px;
          max-width: 79%;
     };
}

