.page {
     color: inherit;
     font-size: inherit;
}

.css-wrapper-enter {
     opacity: 0;
     transform: scale(.7);
     margin-top: 120px;
   }
   .css-wrapper-enter-active {
     opacity: 1;
     margin-top: 0;
     transform: scale(1);
     transition: all 750ms;
   }

   .css-wrapper-enter-done {
     opacity: 1;
     margin-top:0px
   }

   .css-wrapper-exit {
     opacity: 1;
   }

   .css-wrapper-exit-active {
     opacity: 0;
     margin-top: -120px;
     transform: scale(.6);
     transition: all 750ms;
   }

   .css-wrapper-exit-done {
     display: none;
   }
 
